import { useHttp } from "./http";
import { QueryKey, useMutation, useQuery } from "react-query";
import { cleanObjectPrivate } from "./project";
import { Kanban } from "../types/kanban";
import { useAddConfig, useDeleteConfig, useReorderKanbanConfig } from "./use-optimistic-options";
import { Task } from "../types/task";

export const useKanbans = (param?: Partial<Kanban>) => {
  const client = useHttp()
  return useQuery<Kanban[]>(['kanbans', param], () =>
    client('/kanbans', {data: cleanObjectPrivate(param || {}) })
  )
}

export const useAddKanban = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: Partial<Kanban>) => client(`/kanbans`, {
      data: params,
      method: 'POST'
    }),
    useAddConfig(queryKey))
}
export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: Partial<Task>) => client(`/tasks`, {
      data: params,
      method: 'POST'
    }),
    useAddConfig(queryKey))
}
export const useTask = (id?: number) => {
  const client = useHttp()
  return useQuery<Task>(
    ['task', {id}],
    () => client(`/tasks/${id}`),
    {
      enabled: Boolean(id)
    }
  )
}

export const useDeleteKanban = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation(({ id }: { id: number }) => client(`/kanbans/${id}`, {
      method: 'DELETE'
    }),
    useDeleteConfig(queryKey))
}

export interface SortProps {
  fromId: number;
  referenceId: number;
  type: 'before' | 'after';
  fromKanbanId?: number;
  toKanbanId?: number;
}

export const useReorderKanban = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation(
    (params:SortProps) => {
      return client('/kanbans/reorder', {
        data: params,
        method: 'POST'
      })
    },
    useReorderKanbanConfig(queryKey)
  )
}

