import { Button, Drawer, Form, Input, Spin } from "antd";
import { useProjectModal } from "../../utils";
import { UserSelect } from "../../components/user-select";
import { useAddProject, useEditProject } from "../../utils/project";
import { useEffect } from "react";
import { ErrorBox } from "../../components/lib";
import styled from "@emotion/styled";
import { useProjectsQueryKey } from "./util";

export const ProjectModal = () => {
  const { projectCreate, close, editingProject, isLoading } = useProjectModal()
  const useMutateProject = editingProject? useEditProject: useAddProject

  const [form] = Form.useForm()
  const { mutateAsync, error, isLoading: mutateLoading  } = useMutateProject(useProjectsQueryKey())
  const onFinish = (values: any) => {
    mutateAsync({...editingProject, ...values}).then(() => {
      form.resetFields()
      close()
    })
  }
  const onClose = () => {
    form.resetFields()
    close()
  }

  useEffect(() => {
    form.setFieldsValue(editingProject);
  }, [editingProject, form]);

  const title = editingProject? '编辑项目': '添加项目'
  return (
    <Drawer forceRender={true} onClose={onClose} visible={projectCreate} width={'100%'} >
      <Container>
        {
          isLoading? <Spin size={'large'}/>:
            <>
              <h1>{title}</h1>
              <ErrorBox error={error} />
              <Form form={form} layout={'vertical'} style={{width: '40rem'}} onFinish={onFinish} >
                <Form.Item label={'名称'} name={'name'} rules={[{required: true, message: '请输入项目名'}]} >
                  <Input placeholder={'请输入项目名称'} />
                </Form.Item>
                <Form.Item label={'部门'} name={'organization'} rules={[{required: true, message: '请输入部门名'}]} >
                  <Input placeholder={'请输入部门名称'} />
                </Form.Item>
                <Form.Item label={'负责人'} name={'personId'} >
                  <UserSelect defaultOptionName={'负责人'} />
                </Form.Item>
                <Form.Item style={{textAlign: 'right'}} >
                  <Button type={'primary'} htmlType={'submit'} loading={mutateLoading} >提交</Button>
                </Form.Item>
              </Form>
            </>
        }
      </Container>
    </Drawer>
  )
}

const Container = styled.div`
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`


