import React, { } from "react";
type FullbackRender = (props: {error: Error | null}) => React.ReactElement

//https://github.com/bvaughn/react-error-boundary  官方子组件错误兜底
export class ErrorBoundary extends React.Component< React.PropsWithChildren<{ fallbackRender: FullbackRender }>, { error: Error | null }> {
  state = {error: null}
  //当子组件抛出异常，这里会接收到并且调用
  static getDerivedStateFromError(error: Error) {
    return {error}
  }

  render() {
    const { error } = this.state
    const { fallbackRender, children } = this.props
    if(error) {
      return  fallbackRender({error})
    }
    return children
  }
}
