import { FC } from 'react'
import { Dropdown, Menu, Modal, Table, TableProps } from "antd";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { Pin } from "../../components/pin";
import { useDeleteProject, useEditProject } from "../../utils/project";
import { ButtonNoPadding } from "../../components/lib";
import { useProjectModal } from "../../utils";
import { useProjectsQueryKey } from "./util";
import { Project } from "../../types/project";
import { User } from "../../types/user";

interface ListProps extends TableProps<Project> {
  users: User[];
}
export const List :FC<ListProps> = ( props ) =>{

  const { users, ...prop } = props
  const { mutate } = useEditProject(useProjectsQueryKey())

  const pinProject = (id: number) => (pin:boolean) => mutate({ id, pin })

  return (
    <Table
      pagination={false}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render(value, project) {
            return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)} />
          },
        },
        {
          title: '名称',
          dataIndex: 'name',
          key: 'name',
          render(value, project) {
            return <Link to={`${project.id}`}>{project.name}</Link>
          },
          sorter: (a, b) => a.name.localeCompare(b.name)
        },
        {
          title: '部门',
          dataIndex: 'organization',
          key: 'organization'
        },
        {
          title: '创建时间',
          dataIndex: 'created',
          key: 'created',
          render(value, project) {
            return (<span>
          {project.created? dayjs(project.created).format('YYYY-MM-DD'): '无'}
        </span>)
          }
        },
        {
          title: '负责人',
          render(value, project) {
            return <span>
              {users && users.find(user => user.id === project.personId)?.name || '未知'}
            </span>
          }
        },
        {
          render(value, project) {
            return (<More project={project} />)
          }
        }
      ]}
      {
        ...prop
      }
    />
  )
}

export default List;


const More =({ project }: {project: Project}) => {
  const { startEdit } = useProjectModal()
  const editProject = (id: number) => startEdit(id)

  const { mutate: delMutate } = useDeleteProject(useProjectsQueryKey())
  const delProject = (id:number) => delMutate({ id })

  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: '确认删除这个项目吗',
      content: '点击确定删除',
      okText: '确定',
      onOk: () => {
        delProject(id)
      },
      cancelText: '取消'
    })
  }

  return (
    <Dropdown overlay={<Menu>
      <Menu.Item key={'edit'} onClick={() => {editProject(project.id)}} >
        编辑
      </Menu.Item>
      <Menu.Item key={'delete'} onClick={() =>confirmDeleteProject(project.id)} >删除</Menu.Item>
    </Menu>}>
      <ButtonNoPadding type={'link'} >...</ButtonNoPadding>
    </Dropdown>
  )
}

