import { useForm } from "antd/es/form/Form";
import { useEditTask, useTasksModal, useTasksQueryKey } from "./util";
import { useEffect } from "react";
import { Button, Form, Input, Modal } from "antd";
import { UserSelect } from "../../components/user-select";
import { TaskTypeSelect } from "../../components/task-type-select";
import { useDeleteTask } from "../../utils/task";
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 }
}
export const TaskModal = () => {
  const [form] = useForm()
  const { editingTaskId, editingTask, close } = useTasksModal()
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(useTasksQueryKey())
  const {mutateAsync} = useDeleteTask(useTasksQueryKey())
  const onCancel = () => {
    close()
    form.resetFields()
  }
  const onOk = async () => {
    await editTask({...editingTask, ...form.getFieldsValue()})
    close()
  }
  useEffect(() => {
    form.setFieldsValue(editingTask)
  }, [form, editingTask])
  const startDelete = () => {
    Modal.confirm({
      title: '确定删除任务吗',
      okText: '确定',
      cancelText: '取消',
      onOk: async () => {
        await mutateAsync({id: Number(editingTaskId)})
        close()
      }
    })
  }
  return <Modal
    okText={'确认'}
    cancelText={'取消'}
    confirmLoading={editLoading}
    title={'编辑任务'}
    visible={!!editingTaskId}
    onCancel={onCancel}
    onOk={onOk}
    forceRender={true}
  >
    <Form initialValues={editingTask} form={form} {...layout} >
      <Form.Item label={'任务名'} name={'name'} rules={[{required: true, message: '请输入任务名'}]} >
        <Input/>
      </Form.Item>
      <Form.Item label={'经办人'} name={'processorId'}>
        <UserSelect defaultOptionName={'经办人'} />
      </Form.Item>
      <Form.Item label={'类型'} name={'typeId'}>
        <TaskTypeSelect/>
      </Form.Item>
    </Form>
    <div style={{textAlign: 'right'}} >
      <Button size={'large'} style={{fontStyle: '14px'}} onClick={startDelete} >删除</Button>
    </div>
  </Modal>
}
