import { useLocation } from "react-router";
import { useProject } from "../../utils/project";
import { useUrlQueryParam } from "../../utils/url";
import { useCallback, useMemo } from "react";
import { useTask } from "../../utils/kanban";
import { QueryKey, useMutation } from "react-query";
import { useHttp } from "../../utils/http";
// import { Project } from "../../types/project";
import { useEditConfig } from "../../utils/use-optimistic-options";
import { Task } from "../../types/task";

export const useProjectIdInUrl = () => {
  const { pathname } = useLocation()
  const id = pathname.match(/projects\/(\d+)/)?.[1]
  return Number(id)
}

export const useProjectInUrl = () => useProject(useProjectIdInUrl())

export const useKanbanSearchParams = () => ({projectId: useProjectIdInUrl()})

export const useKanbansQueryKey = () => ['kanbans', useKanbanSearchParams()]

export const useTaskSearchParams = () => {
  const [ param ] = useUrlQueryParam([
    'name',
    'typeId',
    'processorId',
    'tagId'
  ])

  const projectId = useProjectIdInUrl()
  return useMemo(() => ({
    projectId,
    typeId: Number(param.typeId) || undefined,
    processorId: Number(param.processorId) || undefined,
    tagId: Number(param.tagId) || undefined,
    name: param.name
  }), [projectId, param])
}

export const useTasksQueryKey = () => ['tasks', useTaskSearchParams()]

export const useTasksModal = () => {
  const [{editingTaskId}, setEditingTaskId] = useUrlQueryParam(['editingTaskId'])
  const { data: editingTask, isLoading } = useTask(Number(editingTaskId))
  const startEdit = useCallback((id: number) => {
    setEditingTaskId({ editingTaskId: id })
  }, [setEditingTaskId])
  const close = useCallback(() => setEditingTaskId({editingTaskId: undefined}), [setEditingTaskId])
  return {
    editingTaskId,
    editingTask,
    startEdit,
    close,
    isLoading
  }
}
export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: Partial<Task>) => {
      return client(`/tasks/${params.id}`, {
        data: params,
        method: 'PATCH'
      })
    },
    useEditConfig(queryKey)
  )
}
